.login-form {
  background-color: #f0f0f0;
  flex-direction: column;
  align-items: center;
  padding: 20px;
  display: flex;
}

.login-form h2 {
  margin-bottom: 10px;
  font-size: 24px;
}

.login-form form {
  flex-direction: column;
  align-items: center;
  margin-top: 20px;
  display: flex;
}

.login-form label {
  margin-bottom: 5px;
}

.login-form input[type="text"], .login-form input[type="password"] {
  border: 1px solid #ccc;
  border-radius: 4px;
  width: 300px;
  margin-bottom: 10px;
  padding: 8px;
  font-size: 16px;
}

.login-form button {
  color: #fff;
  cursor: pointer;
  background-color: #007bff;
  border: none;
  border-radius: 4px;
  padding: 8px 20px;
  font-size: 16px;
}

.login-form button:hover {
  background-color: #0056b3;
}
